'use strict';

var Vue = require('vue');
var Meta = require('vue-meta');
var VueRouter = require('vue-router');
var routes = require('../routes');

Vue.use(Meta);
Vue.use(VueRouter);

var router = new VueRouter({
    mode: 'history',
    hashbang: false,
    routes: routes
});

module.exports = router;


