"use strict";

var views = {
  home: require("./views/home.vue"),
  login: require("./views/login.vue")
};

module.exports = [{
  path: '/',
  name: 'Home',
  component: views.home,
  meta: {
    subtitle: 'Welcome to Feathers-Vue'
  }
}, {
  path: '/login/:type?/:slug?',
  name: 'Login',
  component: views.login,
  meta: {
    subtitle: ''
  }
}];


